  overflow-y: auto;
  height: 100%;
}
@media (min-width: 782px) {
  .block-editor-inserter__main-area {
    width: 350px;
  }
}

.block-editor-inserter__inline-elements {
  margin-top: -1px;
}

.block-editor-inserter__menu.is-bottom::after {
  border-bottom-color: #fff;
}

.components-popover.block-editor-inserter__popover {
  z-index: 99999;
}

.block-editor-inserter__search {
  background: #fff;
  padding: 16px 16px 0 16px;
  position: sticky;
  top: 0;
  z-index: 1;
}
.block-editor-inserter__search .components-search-control__icon {
  right: 20px;
}
.block-editor-inserter__search .components-base-control__field {
  margin-bottom: 0;
}

.block-editor-inserter__tabs {
  display: flex;
  flex-direction: column;
}
.block-editor-inserter__tabs .components-tab-panel__tabs {
  position: sticky;
  top: 64px;
  background: #fff;
  z-index: 1;
  border-bottom: 1px solid #ddd;
}
.block-editor-inserter__tabs .components-tab-panel__tabs .components-tab-panel__tabs-item {
  flex-grow: 1;
  margin-bottom: -1px;
}
.block-editor-inserter__tabs .components-tab-panel__tab-content {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  position: relative;
  z-index: 0;
}

.block-editor-inserter__panel-header {
  display: inline-flex;
  align-items: center;
  padding: 16px 16px 0;
}

.block-editor-inserter__panel-header-patterns {
  padding: 16px 16px 0;
}

.block-editor-inserter__panel-content {
  padding: 16px;
}

.block-editor-inserter__panel-title,
.block-editor-inserter__panel-title button {
  margin: 0 12px 0 0;
  color: #757575;
  text-transform: uppercase;
  font-size: 11px;
  font-weight: 500;
}

.block-editor-inserter__panel-dropdown select.components-select-control__input.components-select-control__input.components-select-control__input {
  height: 36px;
  line-height: 36px;
}

.block-editor-inserter__panel-dropdown select {
  border: none;
}

.block-editor-inserter__block-list {
  flex-grow: 1;
  position: relative;
}

.block-editor-inserter__reusable-blocks-panel {
  position: relative;
  text-align: right;
}

.block-editor-inserter__manage-reusable-blocks {
  display: inline-block;
  margin: 16px;
}

.block-editor-inserter__no-results {
  padding: 32px;
  text-align: center;
}

.block-editor-inserter__no-results-icon {
  fill: #949494;
}

.block-editor-inserter__child-blocks {
  padding: 0 16px;
}

.block-editor-inserter__parent-block-header {
  display: flex;
  align-items: center;
}
.block-editor-inserter__parent-block-header h2 {
  font-size: 13px;
}
.block-editor-inserter__parent-block-header .block-editor-block-icon {
  margin-right: 8px;
}

.block-editor-inserter__preview-container {
  display: none;
  width: 300px;
  background: #fff;
  border-radius: 2px;
  border: 1px solid #ddd;
  position: absolute;
  top: 16px;
  left: calc(100% + 16px);
  max-height: calc(100% - 32px);
  overflow-y: hidden;
}
@media (min-width: 782px) {
  .block-editor-inserter__preview-container {
    display: block;
  }
}
.block-editor-inserter__preview-container .block-editor-block-card {
  padding: 16px;
}
.block-editor-inserter__preview-container .block-editor-block-card__title {
  font-size: 13px;
}

.block-editor-inserter__preview-content {
  min-height: 144px;
  background: #f0f0f0;
  display: grid;
  flex-grow: 1;
  align-items: center;
}

.block-editor-inserter__preview-content-missing {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 144px;
  color: #757575;
  background: #f0f0f0;
}

.block-editor-inserter__tips {
  border-top: 1px solid #ddd;
  padding: 16px;
  flex-shrink: 0;
  position: relative;
}

.block-editor-inserter__manage-reusable-blocks-container {
  padding: 16px;
}

.block-editor-inserter__quick-inserter {
  width: 100%;
  max-width: 100%;
}
@media (min-width: 782px) {
  .block-editor-inserter__quick-inserter {
    width: 350px;
  }
}

.block-editor-inserter__quick-inserter-results .block-editor-inserter__panel-header {
  height: 0;
  padding: 0;
  float: left;
}

.block-editor-inserter__quick-inserter.has-search .block-editor-inserter__panel-content,
.block-editor-inserter__quick-inserter.has-expand .block-editor-inserter__panel-content {
  padding: 16px;
}

.block-editor-inserter__quick-inserter-patterns .block-editor-block-patterns-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 8px;
}
.block-editor-inserter__quick-inserter-patterns .block-editor-block-patterns-list .block-editor-block-patterns-list__list-item {
  margin-bottom: 0;
}
.block-editor-inserter__quick-inserter-patterns .block-editor-block-patterns-list .block-editor-block-preview__container {
  min-height: 100px;
}

.block-editor-inserter__quick-inserter-separator {
  border-top: 1px solid #ddd;
}

.block-editor-inserter__popover.is-quick > .components-popover__content > div {
  padding: 0;
}

.block-editor-inserter__quick-inserter-expand.components-button {
  display: block;
  background: #1e1e1e;
  color: #fff;
  width: 100%;
  height: 44px;
  border-radius: 0;
}
.block-editor-inserter__quick-inserter-expand.components-button:hover {
  color: #fff;
}
.block-editor-inserter__quick-inserter-expand.components-button:active {
  color: #ccc;
}
.block-editor-inserter__quick-inserter-expand.components-button.components-button:focus:not(:disabled) {
  box-shadow: none;
  background: var(--wp-admin-theme-color);
  border-color: var(--wp-admin-theme-color);
}

.block-editor-block-patterns-explorer__sidebar {
  position: absolute;
  top: 60px;
  left: 0;
  bottom: 0;
  width: 280px;
  padding: 24px 32px 32px;
  overflow-x: visible;
  overflow-y: scroll;
}
.block-editor-block-patterns-explorer__sidebar__categories-list__item {
  display: block;
  width: 100%;
  height: 48px;
  text-align: left;
}
.block-editor-block-patterns-explorer__search {
  margin-bottom: 32px;
}
.block-editor-block-patterns-explorer__search-results-count {
  padding-bottom: 32px;
}
.block-editor-block-patterns-explorer__list {
  margin-left: 248px;
}
.block-editor-block-patterns-explorer .block-editor-block-patterns-list {
  display: grid;
  grid-gap: 32px;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 1080px) {
  .block-editor-block-patterns-explorer .block-editor-block-patterns-list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1440px) {
  .block-editor-block-patterns-explorer .block-editor-block-patterns-list {
    grid-template-columns: repeat(3, 1fr);
  }
}
.block-editor-block-patterns-explorer .block-editor-block-patterns-list .block-editor-block-patterns-list__list-item {
  min-height: 240px;
}
.block-editor-block-patterns-explorer .block-editor-block-patterns-list .block-editor-block-preview__container {
  height: inherit;
  min-height: 100px;
  max-height: 800px;
}

.block-editor-post-preview__dropdown {
  padding: 0;
}

.block-editor-post-preview__button-resize.block-editor-post-preview__button-resize {
  padding-left: 40px;
}
.block-editor-post-preview__button-resize.block-editor-post-preview__button-resize.has-icon {
  padding-left: 8px;
}

.block-editor-post-preview__dropdown-content .components-popover__content {
  overflow-y: visible;
}
.block-editor-post-preview__dropdown-content.edit-post-post-preview-dropdown .components-menu-group:first-child {
  padding-bottom: 8px;
}
.block-editor-post-preview__dropdown-content.edit-post-post-preview-dropdown .components-menu-group:last-child {
  margin-bottom: 0;
}
.block-editor-post-preview__dropdown-content .components-menu-group + .components-menu-group {
  padding: 8px;
}

@media (min-width: 600px) {
  .edit-post-header__settings .editor-post-preview,
.edit-site-header__actions .editor-post-preview {
    display: none;
  }
}

@media (min-width: 600px) {
  .edit-post-header.has-reduced-ui .edit-post-header__settings .editor-post-save-draft,
.edit-post-header.has-reduced-ui .edit-post-header__settings .editor-post-saved-state,
.edit-post-header.has-reduced-ui .edit-post-header__settings .block-editor-post-preview__button-toggle {
    transition: opacity 0.1s linear;
  }
}
@media (min-width: 600px) and (prefers-reduced-motion: reduce) {
  .edit-post-header.has-reduced-ui .edit-post-header__settings .editor-post-save-draft,
.edit-post-header.has-reduced-ui .edit-post-header__settings .editor-post-saved-state,
.edit-post-header.has-reduced-ui .edit-post-header__settings .block-editor-post-preview__button-toggle {
    transition-duration: 0s;
    transition-delay: 0s;
  }
}
@media (min-width: 600px) {
  .edit-post-header.has-reduced-ui:not(:hover) .edit-post-header__settings .editor-post-save-draft,
.edit-post-header.has-reduced-ui:not(:hover) .edit-post-header__settings .editor-post-saved-state,
.edit-post-header.has-reduced-ui:not(:hover) .edit-post-header__settings .block-editor-post-preview__button-toggle {
    opacity: 0;
  }
  .edit-post-header.has-reduced-ui:not(:hover) .edit-post-header__settings .block-editor-post-preview__button-toggle.is-opened {
    opacity: 1;
  }
}

